html {
	height: 100%;
	font: italic 1vw Georgia; }


body {
	/*width: 100%;*/
	min-width: 900px;
	max-width: 1700px;
	height: 250%;
	background: url(../img/bg.png) no-repeat;
	@include bg-size(100%,250%);
	background-color: #c6dce9;
	color: #a7a7a7; }


.p0 {
	padding: 0; }


.wrapper {
	position: relative;
	float: left;
	width: 80%;
	height: 75%;
	margin: 2% 10%;
	background-color: #ededed;
	word-wrap: break-word;
	/*overflow: auto;*/ }

/*page-layouts*/
.header {
	width: 100%;
	height: 13%; }


.content {
	margin-top: 1.3%;
	width: 100%;
	height: 85.7%;


	.content-extra {
		float: left;
		width: 96%;
		height: 90%;
		margin: 4.5% 4%;


		.main {
			float: left;
			width: 75%;
			height: 100%;
			margin-right: 3%; }


		.sidebar {
			float: left;
			width: 18.2%;
			height: 100%; } } }

