.sidebar-headline {
	position: relative;
	width: 100%;
	height: 4%;
	background: url(../img/sbar.png) no-repeat;
	@include bg-size;
	margin-bottom: 3.5%;
	@include TNR;
	color: #f6f6f6;

	&__p {
		position: absolute;
		top: 50%;
		margin-top: -0.625rem;
		padding-left: 5%; } }


.sidebar-posts {
	position: relative;
	width: 100%;
	height: 94.2%; }


.sidebar-post {
	width: 100%;
	height: 32%;

	&__link {
		text-decoration: underline;
		padding: 0;
		@include G-normal;
		color: #2b434d; }
	&__image {
		width: 100%; }
	&__p {
		@include G-italic;
		padding-top: 3%; } }
