/*main*/
.main-top {
	width: 100%;
	height: 52%; }

.main-news {
	width: 100%;
	height: 44%;
	margin-top: 6%; }

.main-top-headline {
	position: relative;
	width: 100%;
	height: 8%;
	background: url(../img/mtop.png) no-repeat;
	@include bg-size;
	margin-bottom: 0.7%;

	&__left-line {
		@include TNR;
		color: #f6f6f6;
		position: absolute;
		top: 50%;
		margin-top: -0.625rem;
		padding-left: 0.5%; }

	&__right-line {
		margin-left: 46%;
		@include G-normal;
		color: #828282;
		position: absolute;
		top: 50%;
		margin-top: -0.625rem; } }


.main-top-image {
	width: 100%;
	height: 40%;
	background: url(../img/mimg.png) no-repeat;
	@include bg-size;
	margin-bottom: 1.5%;
	@include G-normal;
	color: #cfcfcf;

	&__text {
		position: absolute;
		width: 30%;
		margin-left: 5%;
		margin-top: 7%;
		background: url(../img/imgtext.png) no-repeat;
		@include bg-size;
		padding: 1%; } }

.main-top-posts {
	width: 100%;
	height: 50.3%; }


.post-extra {
	float: left;
	width: 24%;
	height: 100%;
	background-color: white;
	margin-right: 1.33%;

	&:last-child {
		margin-right: 0; } }


.main-top-post {
	width: 75%;
	margin-left: 13%;
	margin-top: 10%;
	height: 100%;

	&__img {
		float: left;
		width: 100%;
		height: 40%;
		margin-bottom: 5%; }

	&__img--1 {
		background: url(../img/post1.png) no-repeat;
		@include cover; }

	&__img--2 {
		background: url(../img/post2.png) no-repeat;
		@include cover; }

	&__img--3 {
		background: url(../img/post3.png) no-repeat;
		@include cover; }

	&__img--4 {
		background: url(../img/post4.png) no-repeat;
		@include cover; }

	&__link {
		width: 100%;
		text-decoration: underline;
		padding: 0;
		@include G-normal;
		color: #2b434d; }

	&__text {
		width: 95%;
		color: #757575;
		@include G-italic;
		margin-top: 5%; }

	&__icon {
		padding-left: 75%; } }



.main-news-headline {
	position: relative;
	width: 100%;
	height: 9%;
	margin-bottom: 2.5%;

	&__left-line {
		@include TNR;
		color: #f6f6f6;
		position: absolute;
		top: 50%;
		margin-top: -0.625rem;
		padding-left: 0.5%; }

	&__right-line {
		margin-left: 60%;
		@include G-normal;
		color: #828282;
		position: absolute;
		top: 50%;
		margin-top: -0.625rem; }

	&__right-line--headline2 {
		margin-left: 46%; }

	&--1 {
		background: url(../img/dtop.png) no-repeat;
		@include bg-size; }

	&--2 {
		background: url(../img/ddown.png) no-repeat;
		@include bg-size; } }


.main-news-item {
	width: 100%;
	height: 32.7%;
	margin-bottom: 2.5%;

	&__img {
		float: left;
		width: 24%;
		height: 100%;
		margin-right: 3%; }

	&__img--item1 {
		background: url(../img/c1img.png) no-repeat;
		@include cover;
		background-color: #1f5d71; }

	&__img--item2 {
		background: url(../img/c2img.png) no-repeat;
		@include cover;
		background-color: #1f5d71; } }

.main-news-content {
	float: left;
	width: 73%;
	height: 100%;

	&__text {
		width: 100%;
		height: 50%;
		margin: 3% 0 2%;
		@include G-normal;
		color: #757575; } }

.main-news-content-line {
	@include G-normal;
	color: #c3c3c3;
	width: 100%;
	height: 20%;
	padding-top: 1%;

	&__icon {
		padding: 0;
		padding-right: 5%;

		img {
			vertical-align: bottom; } }

	&__tags {
		padding: 0;
		text-decoration: underline;
		@include G-italic(0.9rem);
		color: #2b5e5e; }

	&__tags--first {
		padding-left: 2%; }

	&__tags--last {
		padding-right: 4%; }

	&__social {
		padding: 0;

		img {
			vertical-align: bottom; } }

	&__social--left {
		padding-left: 2%; } }


/*main end*/
