.header-line {
	position: relative;
	width: 100%;
	height: 25%;
	background: url(../img/icon.png) no-repeat;
	background-size: 100% 100%;

	&__p {
		@include G-italic;
		position: absolute;
		top: 50%;
		margin-top: -0.5rem;
		margin-left: 9%; } }

.header-logo {
	width: 100%;
	height: 100%;
	background: url(../img/bmenu.png) no-repeat;
	@include bg-size; }


.menu-upper {
	position: absolute;
	margin: 0;
	margin-top: 3%;
	margin-left: 21%;
	padding: 4px;
	@include TNR(0.9rem);
	color: #717b7f;

	li {
		display: inline;
		margin-right: 5px;
		padding: 1px;

		a:hover {
			text-decoration: underline; }
		a:visited {
			color: inherit; } } }

.menu-lower {
	position: absolute;
	margin: 0;
	margin-top: 6%;
	margin-left: 21%;
	padding: 4px;
	@include TNR;
	color: #f6f6f6;

	li {
		display: inline;
		margin-right: 1px;
		padding: 1px;

		a:hover {
			background-color: #092f3b; }
		a:visited {
			color: inherit; } } }
