@mixin cover {
	background-size: cover; }

@mixin bg-size($width: 100%, $height: 100%) {
	background-size: $width $height; }

@mixin G-normal {
	font-family: Georgia;
	font-size: 0.9rem;
	font-style: normal; }

@mixin G-italic($size: 0.8rem) {
	font-family: Georgia;
	font-size: $size;
	font-style: italic; }

@mixin TNR($size: 1.1rem) {
	font-family: Times New Roman;
	font-size: $size;
	font-style: normal; }
